// 
import componentName from './components/src/index.vue'

// 
const components = [
  componentName,
]

//  install  Vue  use 
const install = function (Vue) {
  // 
  components.map(component => Vue.component(component.name, component))
}

// 
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}

export default {
  //  install Vue.use() 
  install,
  componentName
}



